import React, { Component } from 'react';
import GCN_TABLE from './GCN_TABLE';
import EVENT_TABLE from './EVENT_TABLE';
import CIRC_TABLE from './CIRC_TABLE';
import OBS_TABLE from './OBS_TABLE';
import NOTICE_TABLE from './NOTICE_TABLE';

class ADMIN extends Component {
    
    render() {
        if (this.props.adminTable === 0){
            return <GCN_TABLE />  
        } else if (this.props.adminTable === 1){
            return <EVENT_TABLE />  
        } else if (this.props.adminTable === 2){
            return <CIRC_TABLE />  
        } else if (this.props.adminTable === 3){
            return <OBS_TABLE />  
        } else if (this.props.adminTable === 4){
            return <NOTICE_TABLE />  
        }
    }
}

export default ADMIN;


