/** @flow */

import React, { Component } from 'react';
import {Table, Column, AutoSizer, defaultTableRowRenderer, SortDirection, SortIndicator} from 'react-virtualized';
import 'react-virtualized/styles.css';
import { LabeledInput, InputRow } from './LabeledInput'
import styles from './TABLE.module.css'
import ToggleButton from 'react-toggle-button'
import CachedIcon from '@material-ui/icons/Cached'
import { CoffeeLoading } from 'react-loadingg';

import { Button, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle } from '@material-ui/core';

class GCN_TABLE extends Component {
    
    constructor(props) {
        super(props);

        this.state = {
            circulars: [],
            circulars_selected: [],
            events: [],
            events_selected: [],
            sortBy: "id",
            sortByEvt: "event",
            body: [],
            sortDirection: SortDirection.DESC,
            sortDirectionEvt: SortDirection.DESC,
            selectedIdx: -1 ,
            selectedEvtIdx: -1,
            scrollToIndex: undefined,
            enteredIdx: undefined,
            enteredEvent: undefined,
            locType: true,
            showEvt: true,
            showCir: true,
            grb: true,
            gw: true,
            nu: true,
            others: true,
            cirClose: false,
            radio: false,
            optical: false,
            xRay: false,
            gammaRay: false,
            HE: false,
            VHE: false,
            init: false,
        };

        this._initDatabase = this._initDatabase.bind(this);

        this._sort = this._sort.bind(this);
        this._sortList = this._sortList.bind(this);
        this._sortEvt = this._sortEvt.bind(this);
        this._sortEvtList = this._sortEvtList.bind(this);
        
        this._headerRenderer = this._headerRenderer.bind(this);
        this._headerRendererEvt = this._headerRendererEvt.bind(this);
        
        this._showSelectedType = this._showSelectedType.bind(this)
        this._showSelectedEM = this._showSelectedEM.bind(this)
        
        this._rowClassName = this._rowClassName.bind(this);
        
        this._onRowClick = this._onRowClick.bind(this);
        this._onRowClickEvt = this._onRowClickEvt.bind(this);
        this._onScrollToRowChange = this._onScrollToRowChange.bind(this);
        this._onCloseClick = this._onCloseClick.bind(this);
        this._onHeaderClick = this._onHeaderClick.bind(this);
        
        this._searchEvent = this._searchEvent.bind(this);
        
        this._updatingButtons = this._updatingButtons.bind(this);

    
    }

    componentDidMount() {
        this.getDatabase();
    }

    getDatabase = () => {
        fetch('circular')
        .then(response => response.json())
        .then(response => this.setState({circulars: response.data}))
        .catch(err => console.error(err))

        fetch('event')
        .then(response => response.json())
        .then(response => this.setState({events: response.data}))
        .catch(err => console.error(err))

    }

    getBody({rowData}) {
        fetch('cbody/'+rowData.id)
        .then(response => response.json())
        .then(response => this.setState({body: response.data}))
        .catch(err => console.error(err))
    }

    _initDatabase() {
        const { circulars } = this.state
        if (circulars.length !== 0){
            this._sortList()
            this._sortEvtList()
            this.setState({init:true})
        }
        
    }

    _sort({ sortBy, sortDirection }) {
        this._sortList()
        this.setState({ sortBy, sortDirection});
    }

    _sortEvt({ sortBy, sortDirection }) {
        this._sortEvtList()
        this.setState({ sortByEvt:sortBy, sortDirectionEvt:sortDirection});
    }

    _sortList(){
        const { sortDirection } = this.state
        const { circulars } = this.state
        
        const sortedCirculars = circulars.sort(function(a, b){
            if (sortDirection === SortDirection.DESC){
                return parseInt(b.id, 10) - parseInt(a.id, 10)
            } else {
                return parseInt(a.id, 10) - parseInt(b.id, 10)
            }
        })

        this.setState({circulars: sortedCirculars})

        const {circulars_selected} = this.state

        if (circulars_selected.length === 0){
            this.setState({circulars_selected: sortedCirculars})
        } else {
            this.setState({circulars_selected:
                circulars_selected.sort(
                function(a, b){
                    if (sortDirection === SortDirection.DESC){
                        return parseInt(a.id, 10) - parseInt(b.id, 10)
                    } else {
                        return parseInt(b.id, 10) - parseInt(a.id, 10)
                    }
                })
            })
        }
    }

    _sortEvtList(){
        const { sortDirectionEvt } = this.state
        const { events } = this.state
        const sortedEvents = events.sort(function(a, b){
            var a_date = a.event.split(' ');
            var b_date = b.event.split(' ');
            if (a_date[0] === 'G') {
                return 0
            } else if (b_date[0] === 'G') {
                return -1
            } else if (sortDirectionEvt === SortDirection.DESC){
                return (parseInt(b_date[1].slice(0, 6), 10) - parseInt(a_date[1].slice(0, 6), 10));
            } else {
                return (parseInt(a_date[1].slice(0, 6), 10) - parseInt(b_date[1].slice(0, 6), 10));
            }
        })
        
        this.setState({events:sortedEvents})
            
        const {events_selected} = this.state

        if (events_selected.length === 0){
            this.setState({events_selected: sortedEvents})
        } else {
            this.setState({events_selected:
                events_selected.sort(
                function(a, b){
                    var a_date = a.event.split(' ');
                    var b_date = b.event.split(' ');
                    if (a_date[0] === 'G') {
                        return 0
                    } else if (b_date[0] === 'G') {
                        return -1
                    } else if (sortDirectionEvt === SortDirection.DESC){
                        return (parseInt(a_date[1].slice(0, 6), 10) - parseInt(b_date[1].slice(0, 6), 10));
                    } else {
                        return (parseInt(b_date[1].slice(0, 6), 10) - parseInt(a_date[1].slice(0, 6), 10));
                    }
                
                })
            }) 
        }

    }

    _headerRenderer({ label, dataKey, sortBy, sortDirection }) {
        return (
            <div>
            { label }
            { sortBy === dataKey && <SortIndicator sortDirection={sortDirection} />}
            </div>
        );
    }


    _headerRendererEvt({ label, dataKey, sortBy, sortDirection }) {
        return (
            <div>
            { label }
            { sortBy === dataKey && <SortIndicator sortDirection={sortDirection} />}
            </div>
        );
    }


    cellRenderer_cir = ({ rowData, rowIndex }) => {
        const { circulars } = this.state
        const cirNum = circulars.filter((element) => {return (element.evtid === rowData.id)})
        return <div>{cirNum.length}</div>
    };

    cellRenderer_evt = ({ rowData, rowIndex }) => {
        const { events } = this.state
        if (rowData.evtid!==999999 ) {
            const evtName = events.filter((element) => {return (element.id === rowData.evtid)})[0]
            return <div>{evtName.event}</div>
        } else {
            return <div></div>
        }
    };

    cellRenderer_obs = ({ rowData, rowIndex }) => {
        if (rowData.detector === null) {
            return <div>{rowData.telescope}</div>
        } else {
            return <div>{rowData.telescope}/{rowData.detector}</div>
        }
    };

    cellRenderer_mm = ({ rowData, rowIndex }) => {

        if (rowData.messenger === 'EM') {
            return <div>{rowData.wavelength}</div>    
        } else {
            return <div>{rowData.messenger}</div>
        }
    };

    cellRenderer_mail = ({ rowData, rowIndex }) => {
        return (
            <div>
            <a href={`mailto:${rowData.sender}`}
            target="_blank" rel="noopener noreferrer"><i class="fa fa-envelope-o"></i></a>
            </div>
        )

    };

    rowRenderer = prevProps => {
        const { index, style, key, rowData, className } = prevProps
        const { selectedIdx, body } = this.state

        if (index === selectedIdx && body.length !== 0) {
            return (
            <div
            style={{ ...style, display: "flex", flexDirection: "column" }}
            key={key}
            className={className}
            >
            {defaultTableRowRenderer({
            ...prevProps,
            style: { width: style.width, height: style.height, color:'white', fontWeight: "bold", backgroundColor: '#5c6bc0'}
            })}
            <Dialog
            open={ this.state.cirClose }
            maxWidth={"md"}
            aria-labelledby="alert-dialog-title"
            aria-describedby="alert-dialog-description"
            >
                <DialogTitle id="alert-dialog-title"><div style={{fontSize:"0.9em"}}>{rowData.subject}</div></DialogTitle>
                <DialogContent>
                <DialogContentText id="alert-dialog-description">
                <pre style={{overflow: "auto", whiteSpace: "pre-wrap", wordWrap: "break-word"}}>
                {body[0].body}</pre><br/><br/>
                </DialogContentText>
                </DialogContent>
                <DialogActions>
                <Button onClick={this._onCloseClick} color="primary" autoFocus>
                Close
                </Button>
                </DialogActions>
            </Dialog>
            </div>
        );
        } else{
            return defaultTableRowRenderer({...prevProps, style:{...style}});    
        }
    };

    rowRendererEvt = prevProps => {
        const { index, style } = prevProps
        const { selectedEvtIdx } = this.state

        if (index === selectedEvtIdx) {
            return defaultTableRowRenderer({...prevProps, style:{...style, color:'white', fontWeight: "bold", backgroundColor: '#5c6bc0'}})
        } else {
            return defaultTableRowRenderer({...prevProps, style:{...style}});    
        }
    };

    render() {

        const { circulars, circulars_selected, events_selected, sortBy, sortByEvt, sortDirection, sortDirectionEvt, scrollToIndex, enteredIdx, enteredEvent, init, locType } = this.state

        const { grb, gw, nu, others, radio, optical, xRay, gammaRay, HE, VHE } = this.state

        const _getDatum_circular = index => circulars_selected[index % circulars_selected.length];
        
        const rowGetter_circular = ({ index }) => _getDatum_circular(index);

        const _getDatum_event = index => events_selected[index % events_selected.length];
        
        const rowGetter_event = ({ index }) => _getDatum_event(index);

        if (init===false){
            this._initDatabase()

        } else {
            this._updatingButtons()
        }

        if (circulars.length === 0){
            return <CoffeeLoading />
        } else {
            return (
            <div style={{maxWidth:"1250px"}}>
            <InputRow>
            <div class={ styles.cboxContainer }>
            <LabeledInput
                label="Search for"
                name="searchEvent"
                placeholder="e.g., GRB 190114C"
                onChange={this._searchEvent}
                value={enteredEvent || ''}
              />
            </div>
            <div class={ styles.cboxContainer }>
            <LabeledInput
                label="Circular to"
                name="onScrollToRow"
                placeholder="Index..."
                onChange={this._onScrollToRowChange}
                value={enteredIdx || ''}
              />
            </div>
            <div class={ styles.cboxContainer } style={{width: "230px"}}>
            <div class={ styles.cboxLabel }>Event Type</div>
                <ul class={ styles.ksCboxtags }>
                <li><input
                  aria-label="Gamma-ray bursts"
                  checked={grb}
                  className= "checkbox"
                  type="checkbox"
                  id="grb"
                  onChange={event =>
                    this._showSelectedType(event.target.checked, 'grb')
                  }
                /><label for="grb">
                GRB
                </label></li>
                <li><input
                  aria-label="Gravitational waves"
                  checked={gw}
                  className= "checkbox"
                  type="checkbox"
                  id="gw"
                  onChange={event =>
                    this._showSelectedType(event.target.checked, 'gw')
                  }
                /><label for="gw">
                GW
                </label></li>
                <li><input
                  aria-label="Neutrinos"
                  checked={nu}
                  className= "checkbox"
                  type="checkbox"
                  id="nu"
                  onChange={event =>
                    this._showSelectedType(event.target.checked, 'nu')
                  }
                /><label for="nu">
                &nu;
                </label></li>
                <li><input
                  aria-label="others"
                  checked={others}
                  className= "checkbox"
                  type="checkbox"
                  id="others"
                  onChange={event =>
                    this._showSelectedType(event.target.checked, 'other')
                  }
                /><label for="others">
                others
                </label></li>
                </ul>
            </div>

            <div class={ styles.cboxContainer } style={{width: "320px"}}>
            <div class={ styles.cboxLabel }>Wavelength</div>
                <ul class={ styles.ksCboxMW }>
                <li><input
                  aria-label="Radio"
                  checked={radio}
                  className= "checkbox"
                  type="checkbox"
                  id="radio"
                  onChange={event =>
                    this._showSelectedEM(event.target.checked, 'radio')
                  }
                /><label for="radio">
                Radio
                </label></li>
                <li><input
                  aria-label="Optical"
                  checked={optical}
                  className= "checkbox"
                  type="checkbox"
                  id="optical"
                  onChange={event =>
                    this._showSelectedEM(event.target.checked, 'optical')
                  }
                /><label for="optical">
                optical
                </label></li>
                <li><input
                  aria-label="X-ray"
                  checked={xRay}
                  className= "checkbox"
                  type="checkbox"
                  id="xRay"
                  onChange={event =>
                    this._showSelectedEM(event.target.checked, 'xRay')
                  }
                /><label for="xRay">
                X-ray
                </label></li>
                <li><input
                  aria-label="&gamma;-ray"
                  checked={gammaRay}
                  className= "checkbox"
                  type="checkbox"
                  id="gammaRay"
                  onChange={event =>
                    this._showSelectedEM(event.target.checked, 'gammaRay')
                  }
                /><label for="gammaRay">
                &gamma;-ray
                </label></li>
                <li><input
                  aria-label="High-energy"
                  checked={HE}
                  className= "checkbox"
                  type="checkbox"
                  id="HE"
                  onChange={event =>
                    this._showSelectedEM(event.target.checked, 'HE')
                  }
                /><label for="HE">
                HE
                </label></li>
                <li><input
                  aria-label="Very-high-energy"
                  checked={VHE}
                  className= "checkbox"
                  type="checkbox"
                  id="VHE"
                  onChange={event =>
                    this._showSelectedEM(event.target.checked, 'VHE')
                  }
                /><label for="VHE">
                VHE
                </label></li>
                </ul>
            </div>
            </InputRow>

            
            <div style={{paddingTop:"10pt", float:"left", width:"200pt"}}>
            <div style={{color: "#454545", fontSize: ".75em", fontWeight: 100, width:"70pt", float:"left"}}>
            Event table </div>
            <div style={{float:"left"}}><ToggleButton
                value={ this.state.showEvt || false }
                thumbStyle={{borderRadius: 2 }}
                trackStyle={{borderRadius: 2 }}
                colors={{
                    activeThumb: {
                      base: 'rgb(250,250,250)',
                    },
                    inactiveThumb: {
                      base: 'rgb(250,250,250)',
                    },
                    active: {
                      base: '#7986cb',
                      hover: '#9fa8da',
                    },
                    inactive: {
                      base: 'rgb(65,66,68)',
                      hover: 'rgb(95,96,98)',
                    }
                  }}
                onToggle={(value) => {
                this.setState({
                  showEvt: !value,
                })
                }} /></div>
            </div>
            {this.state.showEvt && (
            <AutoSizer disableHeight>
                {({width}) => (
                <Table
                width={width}
                height={(this.state.showCir)? 300:600}
                headerHeight={30}
                rowHeight={30}
                rowCount={events_selected.length}
                rowGetter={rowGetter_event}
                sort={this._sortEvt}
                sortBy={sortByEvt}
                sortDirection={sortDirectionEvt}
                rowRenderer={this.rowRendererEvt}
                onRowClick={this._onRowClickEvt}
                onHeaderClick={this._onHeaderClick}
                rowClassName = {this._rowClassName}
                >


                <Column 
                    disableSort
                    label="Type" 
                    dataKey="eventType" 
                    width={60}
                />

                <Column 
                    disableSort={false}
                    label="Event" 
                    dataKey="event" 
                    width={130}
                />

                <Column 
                    disableSort
                    cellRenderer={this.cellRenderer_cir}
                    label="Circulars" 
                    dataKey="circulars" 
                    width={110}
                    style={{textIndent:"20pt"}}
                />

                <Column
                    disableSort
                    label="Trigger"
                    dataKey="trigger"
                    width={290} 
                />
                
                <Column
                    disableSort
                    label=<div>RA <CachedIcon style={{fontSize: "10px", cursor: "pointer"}} /></div>
                    dataKey={(locType)?"ra":"raHMS"}
                    width={240} 
                />

                <Column
                    disableSort
                    label=<div>DEC <CachedIcon style={{fontSize: "10px", cursor: "pointer"}} /></div>
                    dataKey={(locType)?"dec":"decDMS"}
                    width={240} 
                />

                <Column
                    disableSort
                    label="Error"
                    dataKey="error"
                    width={180}
                />

                </Table>
                )}
             </AutoSizer>)}

            <div style={{paddingTop:"10pt", float:"left", width:"200pt"}}>
            <div style={{color: "#454545", fontSize: ".75em", fontWeight: 100, width:"70pt", float:"left"}}>
            Circular table </div><div style={{float:"left"}}><ToggleButton
                value={ this.state.showCir || false }
                thumbStyle={{borderRadius: 2 }}
                trackStyle={{borderRadius: 2 }}
                colors={{
                    activeThumb: {
                      base: 'rgb(250,250,250)',
                    },
                    inactiveThumb: {
                      base: 'rgb(250,250,250)',
                    },
                    active: {
                      base: '#7986cb',
                      hover: '#9fa8da',
                    },
                    inactive: {
                      base: 'rgb(65,66,68)',
                      hover: 'rgb(95,96,98)',
                    }
                  }}
                onToggle={(value) => {
                this.setState({
                  showCir: !value,
                })
                }} /></div>
            </div>
            {this.state.showCir && (
            <AutoSizer disableHeight>
                {({width}) => (
                <Table
                width={width}
                height={(this.state.showEvt)? 300:600}
                headerHeight={30}
                rowHeight={30}
                rowCount={circulars_selected.length}
                rowGetter={rowGetter_circular}
                sort={this._sort}
                sortBy={sortBy}
                sortDirection={sortDirection}
                rowRenderer={this.rowRenderer}
                onRowClick={this._onRowClick}
                scrollToIndex={scrollToIndex}
                rowClassName = {this._rowClassName}
                >

                <Column
                    disableSort={false}
                    headerRenderer={this._headerRenderer}
                    label="#"
                    dataKey="id"
                    width={50} 
                />

                <Column 
                    disableSort
                    label="Subject" 
                    dataKey="subject" 
                    width={750}
                />

                <Column 
                    disableSort
                    cellRenderer={this.cellRenderer_evt}
                    label="Event" 
                    dataKey="evtid" 
                    width={150}
                />

                <Column
                    disableSort
                    cellRenderer={this.cellRenderer_obs}
                    label="Observatory"
                    dataKey="telescope"
                    width={150} 
                />
                
                <Column
                    disableSort
                    cellRenderer={this.cellRenderer_mm}
                    label="MW/MM"
                    dataKey="messenger"
                    width={100} 
                />

                <Column 
                    disableSort
                    cellRenderer={this.cellRenderer_mail}
                    label=" "
                    dataKey="sender"
                    width={50}
                />

                </Table>
                )}
            </AutoSizer>)}
            </div>
            )
        }
    };

    _onScrollToRowChange(event) {

        const { circulars_selected, ordered } = this.state;
        const rowCount =  circulars_selected.length
        const enteredIdx = parseInt(event.target.value, 10)
        var Index = undefined

        const selectedCircular = circulars_selected.filter((element) => {return element.id === enteredIdx})[0]
        
        if(typeof selectedCircular !== "undefined")
            Index = selectedCircular.id
        
        let scrollToIndex = Math.max(
          0,
          (rowCount - Index - 1 || 0),
        );

        if (!ordered)
            scrollToIndex = rowCount-scrollToIndex-1

        if (isNaN(scrollToIndex)) {
          scrollToIndex = undefined;
        }

        this.setState({scrollToIndex});
        this.setState({enteredIdx});
    }

    _showSelectedType (value, type) {

        const {grb, gw, nu, others, circulars, events} = this.state
        var filtered

        if (type==='grb'){
            this.setState({grb: value})
            filtered = [value, gw, nu, others]
        } else if (type==='gw'){
            this.setState({gw: value})
            filtered = [grb, value, nu, others]
        } else if (type==='nu'){
            this.setState({nu: value})
            filtered = [grb, gw, value, others]
        } else if (type==='other'){
            this.setState({others: value})
            filtered = [grb, gw, nu, value]
        }

        this.setState({events_selected: events.filter((element) => {
            return (
            ((element.eventType === 'GRB') && filtered[0])||
            ((element.eventType === 'GW') && filtered[1])||
            ((element.eventType === '\u03BD') && filtered[2])||
            ((element.eventType === 'others') && filtered[3])
            )
        })
        })

        this.setState({ circulars_selected: circulars.filter(function(circular)
            {
                const event = events.filter((element) => { return (element.id === circular.evtid)})[0]
                var tempCir
                if (event !== undefined){
                    if (((event.eventType === 'GRB') && filtered[0])||
                        ((event.eventType === 'GW') && filtered[1])||
                        ((event.eventType === '\u03BD') && filtered[2])){
                        tempCir = circular
                    }
                } else if (filtered[3]) {
                    tempCir = circular
                } else {
                    tempCir = undefined
                }
                return tempCir
            }
        )})
    }

    _showSelectedEM (value, type) {

        const {circulars, radio, optical, xRay, gammaRay, HE, VHE} = this.state
        var filtered

        if (type==='radio'){
            filtered = [value, optical, xRay, gammaRay, HE, VHE]
        } else if (type==='optical'){
            filtered = [radio, value, xRay, gammaRay, HE, VHE]
        } else if (type==='xRay'){
            filtered = [radio, optical, value, gammaRay, HE, VHE]
        } else if (type==='gammaRay'){
            filtered = [radio, optical, xRay, value, HE, VHE]
        } else if (type==='HE'){
            filtered = [radio, optical, xRay, gammaRay, value, VHE]
        } else if (type==='VHE'){
            filtered = [radio, optical, xRay, gammaRay, HE, value]
        }
        

        this.setState({circulars_selected: circulars.filter((element) => {
            return (
            ((element.wavelength === 'radio') && filtered[0])||
            ((element.wavelength === 'optical') && filtered[1])||
            ((element.wavelength === 'X-ray') && filtered[2])||
            ((element.wavelength === '\u03B3-ray') && filtered[3])||
            ((element.wavelength === 'HE') && filtered[4])||
            ((element.wavelength === 'VHE') && filtered[5])
            )
        })
        })

        this.setState({radio:false, optical:false, xRay:false, gammaRay:false, HE:false, VHE:false})

    }

    _rowClassName({index}) {
        if (index < 0) {
            return styles.headerRow;
        } else {
            return index % 2 === 0 ? styles.evenRow : styles.oddRow;
        }
    }

    _searchEvent(event) {
        const {circulars, events} = this.state
        const enteredEvent = event.target.value
        this.setState({circulars_selected: circulars.filter((element) => {
            return (element.subject.indexOf(enteredEvent) !== -1)
        })
        })
        this.setState({events_selected: events.filter((element) => {
            return (element.event.indexOf(enteredEvent) !== -1)
        })
        })

        this.setState({enteredEvent});
    }

    _onRowClick ({index, rowData}) {
        
        const { selectedIdx } = this.state
        this.getBody({rowData})

        if ( selectedIdx === index ) {
            this.setState({selectedIdx: -1})
        } else {
            this.setState({selectedIdx: index}) 
            this.setState({cirClose: true})
        }
    }

    _onRowClickEvt({index, rowData}) {

        const { selectedEvtIdx, circulars, events, grb, gw, nu, others } = this.state
        const trueIdx = rowData.id

        if ( selectedEvtIdx === index ) {
            this.setState({selectedEvtIdx: -1})
            
            this.setState({events_selected: events.filter((element) => {
            return (
            ((element.eventType === 'GRB') && grb)||
            ((element.eventType === 'GW') && gw)||
            ((element.eventType === '\u03BD') && nu)||
            ((element.eventType === 'others') && others)
            )})})
            
            this.setState({ circulars_selected: circulars.filter(function(circular) 
                {
                    const event = events.filter((element) => { return (element.id === circular.evtid)})[0]
                    var tempCir
                    if (event !== undefined){
                        if (((event.eventType === 'GRB') && grb)||
                            ((event.eventType === 'GW') && gw)||
                            ((event.eventType === '\u03BD') && nu)){
                            tempCir = circular
                        }
                    } else if (others) {
                        tempCir = circular
                    } else {
                        tempCir = undefined
                    }
                    return tempCir
                }
            )})

        } else {
            
            this.setState({selectedEvtIdx: index}) 
            this.setState({circulars_selected: circulars.filter((element) => {return (element.evtid === trueIdx)})
            })
        }
        this.setState({enteredEvent: undefined})
        this.setState({radio:false, optical:false, xRay:false, gammaRay:false, HE:false, VHE:false})
    }

    _onHeaderClick ({dataKey}) {
        
        if (dataKey === "ra" || dataKey === "dec" ){
            this.setState({locType: false})
        } else if (dataKey === "raHMS" || dataKey === "decDMS" ){
            this.setState({locType: true})
        }
    }

    _onCloseClick () {
        this.setState({cirClose:false})
        this.setState({selectedIdx:-1})
        this.setState({body:[]})
    }

    _updatingButtons() {
        const { circulars_selected, radio, optical, xRay, gammaRay, HE, VHE } = this.state

        if (circulars_selected.length !== 0){
            var i;
            const len = circulars_selected.length
            for (i = 0; i < len; i++) {
                if (circulars_selected[i]['wavelength'] === 'radio' && !(radio)) {
                    this.setState({radio: true})
                } else if (circulars_selected[i]['wavelength'] === 'optical' && !(optical)){
                    this.setState({optical: true})
                } else if (circulars_selected[i]['wavelength'] === 'X-ray' && !(xRay)){
                    this.setState({xRay: true})
                } else if (circulars_selected[i]['wavelength'] === '\u03B3-ray' && !(gammaRay)){
                    this.setState({gammaRay: true})
                } else if (circulars_selected[i]['wavelength'] === 'HE' && !(HE)){
                    this.setState({HE: true})
                } else if (circulars_selected[i]['wavelength'] === 'VHE' && !(VHE)){
                    this.setState({VHE: true})
                }
            }
        }
    }
}

export default GCN_TABLE;
