import React, { Component } from 'react';
import {Table, Column, AutoSizer, defaultTableRowRenderer, SortDirection, SortIndicator} from 'react-virtualized';
import 'react-virtualized/styles.css';
import { LabeledInput, InputRow } from './LabeledInput'
import styles from './TABLE.module.css'
import { Button, Dialog, DialogActions, DialogContent, DialogTitle, TextField, InputLabel, Select, MenuItem } from '@material-ui/core';
import AddIcon from '@material-ui/icons/Add';
import { CoffeeLoading } from 'react-loadingg';

class OBS_TABLE extends Component {
    
    constructor(props) {
        super(props);
        this.state = {
            db: [],
            db_selected: [],
            circulars: [],
            init: false,
            ordered: true,
            editDialog: false,
            addDialog: false,
            sortBy: "id",
            sortDirection: SortDirection.DESC,
            selectedIdx: -1,
            searchText: undefined,
            enteredIdx: undefined, 
            scrollToIndex:undefined, 
            tel: undefined,
            dtr: undefined,
            mw: undefined,
            mm: undefined,
            mwid: undefined,
            fullName: undefined,

        }

        this._rowClassName = this._rowClassName.bind(this)
        this._searchFor = this._searchFor.bind(this)
        this._sortData = this._sortData.bind(this)
        this._sortList = this._sortList.bind(this)
        this._headerRenderer = this._headerRenderer.bind(this)
        this._onScrollToRowChange = this._onScrollToRowChange.bind(this)
        this._onRowClick = this._onRowClick.bind(this)
        this._onModifyClick = this._onModifyClick.bind(this)
        this._onAddClick = this._onAddClick.bind(this)
        this._onAddDiaClick = this._onAddDiaClick.bind(this)
        this._onRemoveClick = this._onRemoveClick.bind(this)
        this._onCloseClick = this._onCloseClick.bind(this)
        this._onValChange = this._onValChange.bind(this)
    }

    componentDidMount() {
        this.getDatabase();
    }

    getDatabase = () => {
        fetch('observatory')
        .then(response => response.json())
        .then(response => this.setState({db: response.data.reverse()}))
        .catch(err => console.error(err))
    
        fetch('circular')
        .then(response => response.json())
        .then(response => this.setState({circulars: response.data.reverse()}))
        .catch(err => console.error(err))
    }

    _initDatabase() {
        const { db } = this.state
        if (db.length !== 0){
            this._sortList()
            this.setState({init:true})
        }
    }

    _sortList() {
        const { sortDirection } = this.state
        const { db } = this.state
        
        const sorted_db = db.sort(function(a, b){
            if (sortDirection === SortDirection.DESC){
                return parseInt(b.id, 10) - parseInt(a.id, 10)
            } else {
                return parseInt(a.id, 10) - parseInt(b.id, 10)
            }
        })

        this.setState({db: sorted_db})

        const { db_selected } = this.state

        if (db_selected.length === 0){
            this.setState({db_selected: sorted_db})
        } else {
            this.setState({db_selected:
                db_selected.sort(
                function(a, b){
                    if (sortDirection === SortDirection.DESC){
                        return parseInt(a.id, 10) - parseInt(b.id, 10)
                    } else {
                        return parseInt(b.id, 10) - parseInt(a.id, 10)
                    }
                })
            })
        }

        
    }
    
    _sortData({ sortBy, sortDirection }) {
        this._sortList()
        this.setState({ sortBy, sortDirection});
    }

    _rowClassName({index}) {
        if (index < 0) {
            return styles.headerRow;
        } else {
            return index % 2 === 0 ? styles.evenRow : styles.oddRow;
        }
    }

    _onScrollToRowChange(e) {

        const { db_selected, ordered } = this.state;
        const rowCount =  db_selected.length
        const enteredIdx = parseInt(e.target.value, 10)
        var Index = undefined

        const scrollTo = db_selected.filter((element) => {return element.id === enteredIdx})[0]
        
        if(typeof scrollTo !== "undefined")
            Index = scrollTo.id
        
        let scrollToIndex = Math.max(
          0,
          (rowCount - Index - 1 || 0),
        );

        if (!ordered)
            scrollToIndex = rowCount-scrollToIndex-1

        if (isNaN(scrollToIndex)) {
          scrollToIndex = undefined;
        }

        this.setState({scrollToIndex});
        this.setState({enteredIdx});
    }

    _searchFor(e) {
        const { db } = this.state
        const searchText = e.target.value

        this.setState({db_selected: db.filter((element) => {
            return (element.telescope.indexOf(searchText) !== -1)
        })
        })

        this.setState({searchText});
    }


    _headerRenderer({ label, dataKey, sortBy, sortDirection }) {
        return (
            <div>
            { label }
            { sortBy === dataKey && <SortIndicator sortDirection={sortDirection} />}
            </div>
        );
    }

    _onRowClick ({rowData, index}) {
        const { selectedIdx } = this.state
        if ( selectedIdx === index ) {
            this.setState({selectedIdx: -1})
        } else {
            this.setState({selectedIdx: index}) 
            this.setState({editDialog: true})
            this.setState({tel: rowData.telescope, dtr: rowData.detector,
            fullName: rowData.full_name,
            mw: rowData.wavelength, mm: rowData.messenger, mwid: rowData.mwid})
        }
    }

    _onAddClick () {
        this.setState({addDialog: true})
    }

    _onAddDiaClick () {
        var { tel, dtr, mw, mm, fullName, mwid } = this.state
        var { db, db_selected, circulars } = this.state
        var id

        if (mwid >= 7){
            id = Math.max(db[db.length-1].id, db[0].id)+1
        } else {
            const db_EM = db.filter((element) => {return (element.id<= 900)})
            id = Math.max(db_EM[db_EM.length-1].id, db_EM[0].id)+1
        }
        
        
        if (dtr===undefined){
            dtr = null
        }

        var added_db = {id: id, telescope: tel, detector: dtr, 
            fullName: fullName, mwid: mwid, mode: 1}

        fetch('observatory/'+id, 
            {
                method: 'POST', 
                body: JSON.stringify(added_db),
                headers: { 'content-type': 'application/json' }
            })
            .catch(err => console.log(err));
        
        added_db = {id: id, telescope: tel, detector: dtr, 
            full_name: fullName, wavelength: mw, messenger: mm, mwid: mwid}
        
        db.unshift(added_db)

        const searchCirs = circulars.filter((element) => {return (element.subject.indexOf(" "+tel+" ") !== -1)})
        var len = searchCirs.length;
        for (var i = 0; i < len; i++){
            var circular = searchCirs[i]
            circular.obsid = id
            circular.mwid = mwid
            
            fetch('circular/'+circular.id, 
            {
                method: 'POST', 
                body: JSON.stringify(circular),
                headers: { 'content-type': 'application/json' }
            })
            .catch(err => console.log(err));

        }

        this.setState({db, db_selected})
        this.setState({addDialog:false})

        this.setState({tel: undefined, dtr: undefined, fullName: undefined,
            mw: undefined, mm: undefined, mwid: undefined})
    }

    _onModifyClick (e) {
        e.preventDefault()
        const { tel, dtr, mw, mm, mwid, fullName } = this.state
        var { db, db_selected } = this.state
        const id=e.target.getAttribute("id")

        console.log(mwid)

        const updated_db = {id: id, telescope: tel, detector: dtr, 
            fullName: fullName, wavelength: mw, messenger: mm, mwid:mwid, mode:0}

        fetch('observatory/'+id, 
            {
                method: 'POST', 
                body: JSON.stringify(updated_db),
                headers: { 'content-type': 'application/json' }
            })
            .catch(err => console.log(err));

        db.forEach(function (element) {
            
            if (element.id === parseInt(id, 10)) {
                element.telescope = tel
                element.detector = dtr
                element.full_name = fullName
                element.wavelength = mw
                element.messenger = mm
                element.mwid = mwid
            }
        })

        db_selected.forEach(function (element) {
            
            if (element.id === parseInt(id, 10)) {
                element.telescope = tel
                element.detector = dtr
                element.full_name = fullName
                element.wavelength = mw
                element.messenger = mm
            }
        })
        
        this.setState({db, db_selected})
        this.setState({editDialog:false})
        this.setState({selectedIdx:-1})

        this.setState({tel: undefined, dtr: undefined, fullName:undefined,
            mw: undefined, mm: undefined})
    }

    _onRemoveClick (e) {
        e.preventDefault()
        var { db, db_selected, circulars } = this.state
        const id=e.target.getAttribute("id")
        
        const removed = db.filter((element) => {return parseInt(element.id, 10) === parseInt(id, 10)})[0]
        db.splice(db.indexOf(removed), 1)    

        const removed_db = {id: id, mode:-1}
        fetch('observatory/'+id, 
            {
                method: 'POST', 
                body: JSON.stringify(removed_db),
                headers: { 'content-type': 'application/json' }
            })
            .catch(err => console.log(err));


        const searchCirs = circulars.filter((element) => {return (element.obsid === id)})
        var len = searchCirs.length;
        for (var i = 0; i < len; i++){
            var circular = searchCirs[i]
            console.log(circular)
            circular.obsid = undefined
            circular.mwid = undefined
            circular.telescope = undefined
            circular.detector = undefined
            circular.messenger = undefined
            circular.wavelength = undefined
            fetch('circular/'+circular.id, 
            {
                method: 'POST', 
                body: JSON.stringify(circular),
                headers: { 'content-type': 'application/json' }
            })
            .catch(err => console.log(err));

        }

        this.setState({db, db_selected})
        this.setState({editDialog: false})
        this.setState({selectedIdx:-1})

        this.setState({tel: undefined, dtr: undefined, fullName:undefined,
            mw: undefined, mm: undefined})
    }

    _onCloseClick () {
        this.setState({editDialog:false})
        this.setState({addDialog:false})
        this.setState({removeDialog:false})
        this.setState({selectedIdx:-1})
        this.setState({tel: undefined, dtr: undefined, fullName: undefined,
            mw: undefined, mm: undefined})
    }


    _onValChange (e) {
        const name = e.target.name
        const value = e.target.value
        const mmmw = ["radio", "optical", "X-ray", "-ray", "HE", "VHE"]

        if (name==="tel"){
            this.setState({tel: value})
        } else if (name==="dtr"){
            this.setState({dtr: value})
        } else if (name==="fullName"){
            this.setState({fullName: value})
        } else if (name==="mw"){
            this.setState({mwid: mmmw.indexOf(value)+1, mw: value, mm: "EM"})
        } else if (name==="mm"){
            if (value==="EM"){
                this.setState({mm: value})
            } else if (value==="GW"){
                this.setState({mwid: 7, mm: value, mw: undefined})
            } else if (value==="") {
                this.setState({mwid: 8, mm: value, mw: undefined})
            }
            
        }
    }
    
    rowRenderer = prevProps => {
        const { index, style, key, rowData, className } = prevProps
        const { selectedIdx, tel, dtr, mw, mm, fullName } = this.state

        if (index === selectedIdx) {
            return (
            <div
            style={{ ...style, display: "flex", flexDirection: "column" }}
            key={key}
            className={className}
            >
            {defaultTableRowRenderer({
            ...prevProps,
            style: {width:style.width, height:style.height, color:'white', fontWeight: "bold", backgroundColor: '#5c6bc0'}
            })}
            <Dialog
            open={ this.state.editDialog }
            maxWidth={"md"}
            aria-labelledby="alert-dialog-title"
            aria-describedby="alert-dialog-description"
            >
                <DialogTitle><div style={{fontSize:"0.9em"}}>Observatory ID: {rowData.id}</div></DialogTitle>
                <DialogContent>
                <TextField
                    margin="dense"
                    id="tel"
                    label="Telescope"
                    type="text"
                    fullWidth
                    value={ tel }
                    name="tel"
                    onChange={this._onValChange}
                    style={{paddingBottom:"10pt"}}
                  />
                <TextField
                    margin="dense"
                    id="dtr"
                    label="Detector"
                    type="text"
                    fullWidth
                    value={ dtr }
                    name="dtr"
                    onChange={this._onValChange}
                    style={{paddingBottom:"10pt"}}
                />
                <TextField
                    margin="dense"
                    id="fullName"
                    label="Full name"
                    type="text"
                    fullWidth
                    value={ fullName }
                    name="fullName"
                    onChange={this._onValChange}
                    style={{paddingBottom:"10pt"}}
                />

                <InputLabel style={{fontSize: "0.75em"}}>Wavelength</InputLabel>
                <Select
                    value={ mw }
                    name="mw"
                    label="Wavelength"
                    onChange={this._onValChange}
                >
                    <MenuItem value={false} name="mw"></MenuItem>
                    <MenuItem value="radio" name="mw">radio</MenuItem>
                    <MenuItem value="optical" name="mw">optical</MenuItem>
                    <MenuItem value="X-ray" name="mw">X-ray</MenuItem>
                    <MenuItem value="-ray" name="mw">-ray</MenuItem>
                    <MenuItem value="HE" name="mw">HE</MenuItem>
                    <MenuItem value="VHE" name="mw">VHE</MenuItem>
                </Select>
                <div style={{paddingBottom:"10pt"}}></div>
                <InputLabel style={{fontSize: "0.75em"}}>Messenger</InputLabel>
                <Select
                    value={ mm }
                    name="mm"
                    label="Messenger"
                    onChange={this._onValChange}
                >
                    <MenuItem value={false} name="mm"></MenuItem>
                    <MenuItem value="EM" name="mm">EM</MenuItem>
                    <MenuItem value="GW" name="mm">GW</MenuItem>
                    <MenuItem value="" name="mm"></MenuItem>
                </Select>
                </DialogContent>
                <DialogActions>
                <Button onClick={this._onModifyClick} color="primary">
                <div id={rowData.id}>Modify</div>
                </Button>
                <Button onClick={this._onRemoveClick} color="primary">
                <div id={rowData.id}>Remove</div>
                </Button>
                <Button onClick={this._onCloseClick} color="primary" autoFocus>
                Close
                </Button>
                </DialogActions>
            </Dialog>
            </div>
        );
        } else{
            return defaultTableRowRenderer({...prevProps, style:{...style}});    
        }
    };

    render() {

        const { db, db_selected, init, sortBy, sortDirection, searchText, scrollToIndex, enteredIdx } = this.state
        const { tel, dtr, mw, mm, fullName } = this.state

        if (init===false){
            this._initDatabase()
        } 

        const _getDatum = index => db_selected[index % db_selected.length];
        
        const rowGetter = ({ index }) => _getDatum(index);

        if (db.length === 0){
            return <CoffeeLoading />
        } else {
            return (
            <div>
                <InputRow>
                <div class={ styles.cboxContainer }>
                <LabeledInput
                    label="Index to"
                    name="onScrollToRow"
                    placeholder="Index..."
                    onChange={this._onScrollToRowChange}
                    value={enteredIdx || ''}
                  />
                </div>
                <div class={ styles.cboxContainer }>
                <LabeledInput
                    label="Search for"
                    name="searchRow"
                    placeholder="e.g., Swift, Fermi, ..."
                    onChange={this._searchFor}
                    value={searchText || ''}
                  />
                </div>
                <div style={{marginLeft:"auto", marginRight:"0"}}>
                    <Button onClick={this._onAddClick} color="primary">
                    <div><AddIcon /></div>
                    </Button>
                </div>
                </InputRow>
                
                <div>
                    <Dialog
                    open={ this.state.addDialog }
                    maxWidth={"md"}
                    aria-labelledby="alert-dialog-title"
                    aria-describedby="alert-dialog-description"
                    >
                    <DialogTitle id="alert-dialog-title"><div style={{fontSize:"0.9em"}}>Add observatory</div></DialogTitle>
                    <DialogContent>
                    <TextField
                        margin="dense"
                        id="tel"
                        label="Telescope"
                        type="text"
                        fullWidth
                        value={ tel }
                        name="tel"
                        onChange={this._onValChange}
                        style={{paddingBottom:"10pt"}}
                      />
                    <TextField
                        margin="dense"
                        id="dtr"
                        label="Detector"
                        type="text"
                        fullWidth
                        value={ dtr }
                        name="dtr"
                        onChange={this._onValChange}
                        style={{paddingBottom:"10pt"}}
                    />
                    <TextField
                        margin="dense"
                        id="fullName"
                        label="Full name"
                        type="text"
                        fullWidth
                        value={ fullName }
                        name="fullName"
                        onChange={this._onValChange}
                        style={{paddingBottom:"10pt"}}
                    />

                    <InputLabel style={{fontSize: "0.75em"}}>Wavelength</InputLabel>
                    <Select
                        value={ mw }
                        name="mw"
                        label="Wavelength"
                        onChange={this._onValChange}
                    >
                        <MenuItem value={false} name="mw"></MenuItem>
                        <MenuItem value="radio" name="mw">radio</MenuItem>
                        <MenuItem value="optical" name="mw">optical</MenuItem>
                        <MenuItem value="X-ray" name="mw">X-ray</MenuItem>
                        <MenuItem value="-ray" name="mw">-ray</MenuItem>
                        <MenuItem value="HE" name="mw">HE</MenuItem>
                        <MenuItem value="VHE" name="mw">VHE</MenuItem>
                    </Select>
                    <div style={{paddingBottom:"10pt"}}></div>
                    <InputLabel style={{fontSize: "0.75em"}}>Messenger</InputLabel>
                    <Select
                        value={ mm }
                        name="mm"
                        label="Messenger"
                        onChange={this._onValChange}
                    >
                        <MenuItem value={false} name="mm"></MenuItem>
                        <MenuItem value="EM" name="mm">EM</MenuItem>
                        <MenuItem value="GW" name="mm">GW</MenuItem>
                        <MenuItem value="" name="mm"></MenuItem>
                    </Select>
                    </DialogContent>
                    <DialogActions>
                    <Button onClick={this._onAddDiaClick} color="primary">
                    Add
                    </Button>
                    <Button onClick={this._onCloseClick} color="primary" autoFocus>
                    Close
                    </Button>
                    </DialogActions>
                    </Dialog>
                </div>

                <AutoSizer disableHeight>
                {({width}) => (
                <Table
                width={width}
                height={600}
                headerHeight={30}
                rowHeight={30}
                rowCount={db_selected.length}
                rowGetter={rowGetter}
                sort={this._sortData}
                sortBy={sortBy}
                rowRenderer={this.rowRenderer}
                onRowClick={this._onRowClick}
                sortDirection={sortDirection}
                rowClassName={this._rowClassName}
                scrollToIndex={scrollToIndex}
                >

                 <Column
                    disableSort={false}
                    headerRenderer={this._headerRenderer}
                    label="#"
                    dataKey="id"
                    width={50} 
                />

                <Column 
                    disableSort
                    label="Telescope" 
                    dataKey="telescope" 
                    width={200}
                />

                <Column 
                    disableSort
                    label="Detector" 
                    dataKey="detector" 
                    width={200}
                />

                <Column 
                    disableSort
                    label="Full name" 
                    dataKey="full_name" 
                    width={500}
                />

                <Column 
                    disableSort
                    label="Wavelength" 
                    dataKey="wavelength" 
                    width={150}
                />

                <Column
                    disableSort
                    label="Messenger"
                    dataKey="messenger"
                    width={150} 
                />

                </Table>
                )}
                </AutoSizer>
            </div>
        )}
    }
}

export default OBS_TABLE;
