import React, { Component } from 'react';
import {Table, Column, AutoSizer, defaultTableRowRenderer, SortDirection, SortIndicator} from 'react-virtualized';
import 'react-virtualized/styles.css';
import { LabeledInput, InputRow } from './LabeledInput'
import styles from './TABLE.module.css'
import { Button, Dialog, DialogActions, DialogContent, DialogTitle, TextField } from '@material-ui/core';
import { CoffeeLoading } from 'react-loadingg';

class EVENT_TABLE extends Component {
    
    constructor(props) {
        super(props);
        this.state = {
            db: [],
            db_selected: [],
            init: false,
            ordered: true,
            editDialog: false,
            sortBy: "id",
            sortDirection: SortDirection.DESC,
            selectedIdx: -1,
            searchText: undefined,
            enteredIdx: undefined, 
            scrollToIndex:undefined, 
            type: 1,

        }

        this._rowClassName = this._rowClassName.bind(this)
        this._searchFor = this._searchFor.bind(this)
        this._sortData = this._sortData.bind(this)
        this._headerRenderer = this._headerRenderer.bind(this)
        this._onRowClick = this._onRowClick.bind(this)
        this._onRemoveClick = this._onRemoveClick.bind(this)
        this._onCloseClick = this._onCloseClick.bind(this)
        this._showSelectedType = this._showSelectedType.bind(this)
        this._onScrollToRowChange = this._onScrollToRowChange.bind(this)
    }

    componentDidMount() {
        this.getDatabase();
    }

    getDatabase = () => {
    
        fetch('event')
        .then(response => response.json())
        .then(response => this.setState({db: response.data.reverse()}))
        .catch(err => console.error(err))
    }

    _initDatabase() {
        const { db } = this.state
        if (db.length > 0) {
            this.setState({init:true, db_selected: db.filter((element) => {return (element.eventType === 'GRB')})})
        }
    }

    _sortData({ sortBy, sortDirection }) {
        let { db, ordered, db_selected } = this.state
        ordered = !ordered
        db= db.reverse()
        db_selected= db_selected.reverse()
        this.setState({ sortBy, sortDirection, ordered, db, db_selected});
    }

    _rowClassName({index}) {
        if (index < 0) {
            return styles.headerRow;
        } else {
            return index % 2 === 0 ? styles.evenRow : styles.oddRow;
        }
    }

    _onScrollToRowChange(e) {

        const { db_selected, ordered } = this.state;
        const rowCount =  db_selected.length
        const enteredIdx = parseInt(e.target.value, 10)
        var Index = undefined

        const scrollTo = db_selected.filter((element) => {return element.id === enteredIdx})[0]
        
        if(typeof scrollTo !== "undefined")
            Index = scrollTo.id
        
        let scrollToIndex = Math.max(
          0,
          (rowCount - Index - 1 || 0),
        );

        if (!ordered)
            scrollToIndex = rowCount-scrollToIndex-1

        if (isNaN(scrollToIndex)) {
          scrollToIndex = undefined;
        }

        this.setState({scrollToIndex});
        this.setState({enteredIdx});
    }

    _searchFor(e) {
        const { db } = this.state
        const searchText = e.target.value

        this.setState({db_selected: db.filter((element) => {
            return (element.event.indexOf(searchText) !== -1)
        })
        })

        this.setState({searchText});
    }

    _showSelectedType (type) {

        const { db } = this.state

        this.setState({type})


        this.setState({db_selected: db.filter((element) => {
            if (type === 1){
                return (element.eventType === 'GRB') 
            } else if (type === 2){
                return (element.eventType === 'GW')
            } else if (type === 3){
                return (element.eventType === '\u03BD')
            } else {
                return (element.eventType === 'others')
            }
        })
        })
    }

    _headerRenderer({ label, dataKey, sortBy, sortDirection }) {
        return (
            <div>
            { label }
            { sortBy === dataKey && <SortIndicator sortDirection={sortDirection} />}
            </div>
        );
    }

    _onRowClick ({rowData, index}) {
        const { selectedIdx } = this.state
        if ( selectedIdx === index ) {
            this.setState({selectedIdx: -1})
        } else {
            this.setState({selectedIdx: index}) 
            this.setState({editDialog: true})
        }
    }

    _onRemoveClick (e) {
        e.preventDefault()
        var { db, db_selected } = this.state
        const id=e.target.getAttribute("id")
        
        const removed = db.filter((element) => {return parseInt(element.id, 10) === parseInt(id, 10)})[0]

        db.splice(db.indexOf(removed), 1)    
        db_selected.splice(db_selected.indexOf(removed), 1)    

        const removed_db = {id: id, mode:-1}
        fetch('event/'+id, 
            {
                method: 'POST', 
                body: JSON.stringify(removed_db),
                headers: { 'content-type': 'application/json' }
            })
            .catch(err => console.log(err));

        this.setState({db, db_selected})
        this.setState({editDialog: false})
        this.setState({selectedIdx:-1})
    }

    _onCloseClick () {
        this.setState({editDialog:false})
        this.setState({selectedIdx:-1})
    }

    rowRenderer = prevProps => {
        const { index, style, key, rowData, className } = prevProps
        const { selectedIdx } = this.state
        
        if (index === selectedIdx) {
            return (
            <div
            style={{ ...style, display: "flex", flexDirection: "column" }}
            key={key}
            className={className}
            >
            {defaultTableRowRenderer({
            ...prevProps,
            style: {width:style.width, height:style.height, color:'white', fontWeight: "bold", backgroundColor: '#5c6bc0'}
            })}
            <Dialog
            open={ this.state.editDialog }
            maxWidth={"md"}
            aria-labelledby="alert-dialog-title"
            aria-describedby="alert-dialog-description"
            >
                <DialogTitle><div style={{fontSize:"0.9em"}}>Event ID: {rowData.id}</div></DialogTitle>
                <DialogContent>
                <TextField
                    margin="dense"
                    id="type"
                    label="Event Type"
                    type="text"
                    fullWidth
                    value={ rowData.eventType }
                    name="type"
                    style={{paddingBottom:"5pt"}}
                  />
                <TextField
                    margin="dense"
                    id="evt"
                    label="Event"
                    type="text"
                    fullWidth
                    value={ rowData.event }
                    name="evt"
                    style={{paddingBottom:"5pt"}}
                />
                {(rowData.trigger !== null)&&
                    <TextField
                        margin="dense"
                        id="trig"
                        label="Trigger"
                        type="text"
                        fullWidth
                        value={ rowData.trigger }
                        name="trig"
                        style={{paddingBottom:"5pt"}}
                    />  
                }
                
                {(rowData.ra !== null) &&
                    <div>
                    <TextField
                        margin="dense"
                        id="ra"
                        label="RA"
                        type="text"
                        fullWidth
                        value={rowData.ra + " ("+ rowData.raHMS+")"}
                        name="ra"
                        style={{paddingBottom:"5pt"}}
                    />
                    <TextField
                        margin="dense"
                        id="dec"
                        label="DEC"
                        type="text"
                        fullWidth
                        value={rowData.dec + " ("+ rowData.decDMS+")"}
                        name="dec"
                        style={{paddingBottom:"5pt"}}
                    />
                    </div>
                }
                {(rowData.error !== null) &&
                    <TextField
                        margin="dense"
                        id="err"
                        label="Error"
                        type="text"
                        fullWidth
                        value={rowData.error}
                        name="err"
                        style={{paddingBottom:"5pt"}}
                    />
                }
                </DialogContent>
                <DialogActions>
                <Button onClick={this._onRemoveClick} color="primary">
                <div id={rowData.id}>Remove</div>
                </Button>
                <Button onClick={this._onCloseClick} color="primary" autoFocus>
                Close
                </Button>
                </DialogActions>
            </Dialog>
            </div>
        );
        } else{
            return defaultTableRowRenderer({...prevProps, style:{...style}});    
        }
    };


    render() {

        const { db, db_selected, init, sortBy, sortDirection, searchText, scrollToIndex, enteredIdx, type } = this.state

        if (init===false){
            this._initDatabase()
        } 

        const _getDatum = index => db_selected[index % db_selected.length];
        
        const rowGetter = ({ index }) => _getDatum(index);

        
        if (db.length === 0){
            return <CoffeeLoading />
        } else {
            return (
            <div>
                <InputRow>
                <div class={ styles.cboxContainer }>
                <LabeledInput
                    label="Index to"
                    name="onScrollToRow"
                    placeholder="Index..."
                    onChange={this._onScrollToRowChange}
                    value={enteredIdx || ''}
                  />
                </div>
                <div class={ styles.cboxContainer }>
                <LabeledInput
                    label="Search for"
                    name="searchRow"
                    placeholder="e.g., Swift, GRB, ..."
                    onChange={this._searchFor}
                    value={searchText || ''}
                  />
                </div>
                

                <div class={ styles.cboxContainer } style={{width: "230px"}}>
                    <div class={ styles.cboxLabel }>Event Type</div>
                    <ul class={ styles.ksCboxtags }>
                    <li><input
                      aria-label="Gamma-ray bursts"
                      checked={(type===1)? true: false}
                      className= "checkbox"
                      type="checkbox"
                      id="grb"
                      onChange={event =>
                        this._showSelectedType(1)
                      }
                    /><label for="grb">
                    GRB
                    </label></li>
                    <li><input
                      aria-label="Gravitational waves"
                      checked={(type===2)? true: false}
                      className= "checkbox"
                      type="checkbox"
                      id="gw"
                      onChange={event =>
                        this._showSelectedType(2)
                      }
                    /><label for="gw">
                    GW
                    </label></li>
                    <li><input
                      aria-label="Neutrinos"
                      checked={(type===3)? true: false}
                      className= "checkbox"
                      type="checkbox"
                      id="nu"
                      onChange={event =>
                        this._showSelectedType(3)
                      }
                    /><label for="nu">
                    &nu;
                    </label></li>
                    <li><input
                      aria-label="others"
                      checked={(type===4)? true: false}
                      className= "checkbox"
                      type="checkbox"
                      id="others"
                      onChange={event =>
                        this._showSelectedType(4)
                      }
                    /><label for="others">
                    others
                    </label></li>
                    </ul>
                </div>
                </InputRow>

                <AutoSizer disableHeight>
                {({width}) => (
                <Table
                width={width}
                height={600}
                headerHeight={30}
                rowHeight={30}
                rowCount={db_selected.length}
                rowGetter={rowGetter}
                sort={this._sortData}
                sortBy={sortBy}
                rowRenderer={this.rowRenderer}
                onRowClick={this._onRowClick}
                sortDirection={sortDirection}
                rowClassName={this._rowClassName}
                scrollToIndex={scrollToIndex}
                >


                <Column
                    disableSort={false}
                    headerRenderer={this._headerRender}
                    label="#"
                    dataKey="id"
                    width={50} 
                />

                <Column 
                    disableSort
                    label="Type" 
                    dataKey="eventType" 
                    width={60}
                />

                <Column 
                    disableSort
                    label="Event" 
                    dataKey="event" 
                    width={150}
                />

                <Column
                    disableSort
                    label="Trigger"
                    dataKey="trigger"
                    width={350} 
                />
                
                <Column
                    disableSort
                    label="RA"
                    dataKey="ra"
                    width={130} 
                />

                <Column
                    disableSort
                    label="RA_HMS"
                    dataKey="raHMS"
                    width={150} 
                />

                <Column
                    disableSort
                    label="DEC"
                    dataKey="dec"
                    width={130} 
                />

                <Column
                    disableSort
                    label="DEC_DMS"
                    dataKey="decDMS"
                    width={150} 
                />

                <Column
                    disableSort
                    label="Error"
                    dataKey="error"
                    width={150}
                />

                </Table>
                )}
                </AutoSizer>
            </div>
        )}
    }
}

export default EVENT_TABLE;
