/** @flow */

import React, { Component } from 'react';
import {Table, Column, AutoSizer, defaultTableRowRenderer, SortDirection, SortIndicator} from 'react-virtualized';
import 'react-virtualized/styles.css';
import { LabeledInput, InputRow } from './LabeledInput'
import styles from './TABLE.module.css'
import { Button, Dialog, DialogActions, DialogContent, DialogTitle, DialogContentText, InputLabel, Select } from '@material-ui/core';
import { CoffeeLoading } from 'react-loadingg';

class CIRC_TABLE extends Component {
    
    constructor(props) {
        super(props);
        this.state = {
            db: [],
            db_selected: [],
            events: [],
            obs: [],
            body: [],
            init: false,
            ordered: true,
            editDialog: false,
            sortBy: "id",
            sortDirection: SortDirection.DESC,
            selectedIdx: -1,
            searchText: undefined,
            enteredIdx: undefined, 
            scrollToIndex:undefined, 
            obsid: undefined,
            evtid: undefined,

        }

        this._rowClassName = this._rowClassName.bind(this)
        this._searchFor = this._searchFor.bind(this)
        this._sortData = this._sortData.bind(this)
        this._onRowClick = this._onRowClick.bind(this)
        this._onModifyClick = this._onModifyClick.bind(this)
        this._onCloseClick = this._onCloseClick.bind(this)
        this._onValChange = this._onValChange.bind(this)
        this._headerRenderer = this._headerRenderer.bind(this)
        this._onScrollToRowChange = this._onScrollToRowChange.bind(this)
    }

    componentDidMount() {
        this.getDatabase();
    }

    getDatabase = () => {
    
        fetch('circular')
        .then(response => response.json())
        .then(response => this.setState({db: response.data.reverse()}))
        .catch(err => console.error(err))

        fetch('event')
        .then(response => response.json())
        .then(response => this.setState({events: response.data}))
        .catch(err => console.error(err))

        fetch('observatory')
        .then(response => response.json())
        .then(response => this.setState({obs: response.data}))
        .catch(err => console.error(err))
    }

    getBody({rowData}) {
        fetch('cbody/'+rowData.id)
        .then(response => response.json())
        .then(response => this.setState({body: response.data}))
        .catch(err => console.error(err))
    }

    _copyDatabase() {
        const { db } = this.state
        if (db.length > 0) {
            this.setState({init:true, db_selected: db})    
        }
        
    }

    _sortData({ sortBy, sortDirection }) {
        let { db, ordered, db_selected } = this.state
        ordered = !ordered
        db= db.reverse()
        db_selected= db_selected.reverse()
        this.setState({ sortBy, sortDirection, ordered, db, db_selected});
    }
    
    _onRowClick ({rowData, index}) {
        const { selectedIdx } = this.state
        this.getBody({rowData})

        if ( selectedIdx === index ) {
            this.setState({selectedIdx: -1})
        } else {
            this.setState({selectedIdx: index}) 
            this.setState({editDialog: true})
            this.setState({evtid: rowData.evtid, obsid: rowData.obsid})
            //fullName: rowData.full_name,
            //mw: rowData.wavelength, mm: rowData.messenger
        }
    }

    _onModifyClick (e) {
        e.preventDefault()
        const { evtid, obsid, obs } = this.state
        var { db_selected } = this.state
        const id=e.target.getAttribute("id")
        const updated_db = {id: id, evtid: evtid, obsid: obsid}
        
        fetch('circular/'+id, 
            {
                method: 'POST', 
                body: JSON.stringify(updated_db),
                headers: { 'content-type': 'application/json' }
            })
            .catch(err => console.log(err));

        db_selected.forEach(function (element) {
            if (element.id === parseInt(id, 10)) {
                const updatedObs = obs.filter((element) => {return element.id === obsid})[0]
                element.evtid = evtid
                element.obsid = obsid
                element.telescope = updatedObs.telescope
                element.detector = updatedObs.detector
                element.mwid = updatedObs.mwid
                element.wavelength = updatedObs.wavelength
                element.messenger = updatedObs.messenger
            }
        })
        
        this.setState({db_selected})
        this.setState({editDialog:false})
        this.setState({selectedIdx:-1})
        this.setState({evtid: undefined, obsid: undefined})
    }

    _onCloseClick () {
        this.setState({editDialog:false})
        this.setState({selectedIdx:-1})
        this.setState({evtid: undefined, obsid: undefined})
        this.setState({body: []})
    }

    _onValChange (e) {
        const name = e.target.name
        const value = parseInt(e.target.value)
        if (name==="evt"){
            this.setState({evtid: value})
        } else if (name==="obs"){
            this.setState({obsid: value})
        }
    }

    rowRenderer = prevProps => {
        const { index, style, key, rowData, className } = prevProps
        const { selectedIdx, events, obs, obsid, evtid, body } = this.state

        if (index === selectedIdx && body.length !== 0) {
            return (
            <div
            style={{ ...style, display: "flex", flexDirection: "column" }}
            key={key}
            className={className}
            >
            {defaultTableRowRenderer({
            ...prevProps,
            style: { width: style.width, height: style.height, color:'white', fontWeight: "bold", backgroundColor: '#5c6bc0'}
            })}
            <Dialog
            open={ this.state.editDialog }
            maxWidth={"md"}
            aria-labelledby="alert-dialog-title"
            aria-describedby="alert-dialog-description"
            >
                <DialogTitle id="alert-dialog-title"><div style={{fontSize:"0.9em"}}>Circular ID: {rowData.id}</div></DialogTitle>
                <DialogContent>
                <DialogContentText id="alert-dialog-description">
                <pre style={{overflow: "auto", whiteSpace: "pre-wrap", wordWrap: "break-word"}}>
                {body[0].body}</pre><br/><br/>
                </DialogContentText>

                <InputLabel style={{fontSize: "0.75em"}}>Event</InputLabel>
                <Select
                    native
                    value={ evtid }
                    name="evt"
                    label="Event"
                    onChange={this._onValChange}
                >
                    <option key={999999} value={999999}></option>
                    {events.map((element) => (
                        <option key={element.id} value={element.id}>{element.event}</option>
                        ))}
                </Select>
                <div style={{paddingBottom:"10pt"}}></div>
                <InputLabel style={{fontSize: "0.75em"}}>Observatory</InputLabel>
                <Select
                    native
                    value={ obsid }
                    name="obs"
                    label="Observatory"
                    onChange={this._onValChange}
                >
                    <option key={9999} value={9999}></option>
                    {obs.map((obs) => (
                        (obs.detector===null)?
                        <option key={obs.id} value={obs.id}>{obs.telescope}</option>
                        :<option key={obs.id} value={obs.id}>{obs.telescope+"/"+obs.detector}</option>
                        ))}
                    
                    <option key={1001} value={1001}>radio</option>
                    <option key={1002} value={1002}>optical</option>
                    <option key={1003} value={1003}>X-ray</option>
                    <option key={1004} value={1004}>-ray</option>
                    <option key={1005} value={1005}>HE</option>
                    <option key={1006} value={1006}>VHE</option>

                </Select>
                </DialogContent>
                <DialogActions>
                <Button onClick={this._onModifyClick} color="primary">
                <div id={rowData.id}>Modify</div>
                </Button>
                <Button onClick={this._onCloseClick} color="primary" autoFocus>
                Close
                </Button>
                </DialogActions>
            </Dialog>
            </div>
        );
        } else{
            return defaultTableRowRenderer({...prevProps, style:{...style}});    
        }
    };

    _rowClassName({index}) {
        if (index < 0) {
            return styles.headerRow;
        } else {
            return index % 2 === 0 ? styles.evenRow : styles.oddRow;
        }
    }

    _onScrollToRowChange(e) {

        const { db_selected, ordered } = this.state;
        const rowCount =  db_selected.length
        const enteredIdx = parseInt(e.target.value, 10)
        var Index = undefined

        const scrollTo = db_selected.filter((element) => {return element.id === enteredIdx})[0]
        
        if(typeof scrollTo !== "undefined")
            Index = scrollTo.id
        
        let scrollToIndex = Math.max(
          0,
          (rowCount - Index - 1 || 0),
        );

        if (!ordered)
            scrollToIndex = rowCount-scrollToIndex-1

        if (isNaN(scrollToIndex)) {
          scrollToIndex = undefined;
        }

        this.setState({scrollToIndex});
        this.setState({enteredIdx});
    }

    _searchFor(e) {
        const { db } = this.state
        const searchText = e.target.value

        this.setState({db_selected: db.filter((element) => {
            return (element.subject.indexOf(searchText) !== -1)
        })
        })

        this.setState({searchText});
    }


    _headerRenderer({ label, dataKey, sortBy, sortDirection }) {
        return (
            <div>
            { label }
            { sortBy === dataKey && <SortIndicator sortDirection={sortDirection} />}
            </div>
        );
    }

    cellRenderer_evt = ({ rowData, rowIndex }) => {
        const { events } = this.state
        if (rowData.evtid!==999999 ) {
            const evtName = events.filter((element) => {return (element.id === rowData.evtid)})[0]
            return <div>{evtName.event}</div>
        } else {
            return <div></div>
        }
    };

    cellRenderer_obs = ({ rowData, rowIndex }) => {
        if (rowData.detector === null) {
            return <div>{rowData.telescope}</div>
        } else {
            return <div>{rowData.telescope}/{rowData.detector}</div>
        }
    };

    cellRenderer_mm = ({ rowData, rowIndex }) => {

        if (rowData.messenger === 'EM') {
            return <div>{rowData.wavelength}</div>    
        } else {
            return <div>{rowData.messenger}</div>
        }
    };


    render() {

        const { db, db_selected, init, sortBy, sortDirection, searchText, scrollToIndex, enteredIdx } = this.state

        if (init===false){
            this._copyDatabase()
        } 

        const _getDatum = index => db_selected[index % db_selected.length];
        
        const rowGetter = ({ index }) => _getDatum(index);
        
        if (db.length === 0){
            return <CoffeeLoading />
        } else {
            return (
            <div>
                <InputRow>
                <div class={ styles.cboxContainer }>
                <LabeledInput
                    label="Index to"
                    name="onScrollToRow"
                    placeholder="Index..."
                    onChange={this._onScrollToRowChange}
                    value={enteredIdx || ''}
                  />
                </div>
                <div class={ styles.cboxContainer }>
                <LabeledInput
                    label="Search for"
                    name="searchRow"
                    placeholder="e.g., Swift, GRB, ..."
                    onChange={this._searchFor}
                    value={searchText || ''}
                  />
                </div>
                </InputRow>

                <AutoSizer disableHeight>
                {({width}) => (
                <Table
                width={width}
                height={600}
                headerHeight={30}
                rowHeight={30}
                rowCount={db_selected.length}
                rowGetter={rowGetter}
                sort={this._sortData}
                sortBy={sortBy}
                onRowClick={this._onRowClick}
                rowRenderer={this.rowRenderer}
                sortDirection={sortDirection}
                rowClassName={this._rowClassName}
                scrollToIndex={scrollToIndex}
                >

                 <Column
                    disableSort={false}
                    headerRenderer={this._headerRenderer}
                    label="#"
                    dataKey="id"
                    width={50} 
                />

                <Column 
                    disableSort
                    label="Subject" 
                    dataKey="subject" 
                    width={300}
                />

                <Column 
                    disableSort
                    cellRenderer={this.cellRenderer_evt}
                    label="Event" 
                    dataKey="evtid" 
                    width={150}
                />
                
                <Column
                    disableSort
                    cellRenderer={this.cellRenderer_obs}
                    label="Observatory"
                    dataKey="telescope"
                    width={150} 
                />
                
                <Column
                    disableSort
                    cellRenderer={this.cellRenderer_mm}
                    label="MW/MM"
                    dataKey="messenger"
                    width={100} 
                />


                <Column 
                    disableSort
                    label="Sender"
                    dataKey="sender"
                    width={100}
                />

                <Column 
                    disableSort
                    label="Received"
                    dataKey="received"
                    width={150}
                />

                <Column 
                    disableSort
                    label="Body"
                    dataKey="body"
                    width={50}
                />

                </Table>
                )}
                </AutoSizer>
            </div>
        )}
    }
}

export default CIRC_TABLE;
