import React, { Component } from 'react';
import clsx from 'clsx';
import './App.css'
import GCN_TABLE from './components/GCN_TABLE';
import ADMIN from './components/ADMIN';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import Container from '@material-ui/core/Container';
import { Dialog, DialogContent, DialogTitle } from '@material-ui/core';
import styles from './subStyle.module.css'
import { withStyles } from '@material-ui/core/styles';

import Drawer from '@material-ui/core/Drawer';
import Box from '@material-ui/core/Box';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import List from '@material-ui/core/List';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';
import SupervisorAccountIcon from '@material-ui/icons/SupervisorAccount';
import MailOutlineIcon from '@material-ui/icons/MailOutline';
import { mainListItems, adminListItems } from './components/LISTITEMS';

const useStyles = theme => ({
  root: {
    display: 'flex',
  },

  appBar: {
    transition: theme.transitions.create(['margin', 'width'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
  },

  appBarShift: {
    width: `calc(100% - ${240}px)`,
    marginLeft: 240,
    transition: theme.transitions.create(['margin', 'width'], {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },

  menuButton: {
    marginRight: theme.spacing(2),
  },

  title: {
    flexGrow: 1,
  },

  hide: {
    display: 'none',
  },
  drawer: {
    width: 240,
    flexShrink: 0,
  },
  drawerPaper: {
    width: 240,
  },
  drawerHeader: {
    display: 'flex',
    alignItems: 'center',
    padding: theme.spacing(0, 1),
    // necessary for content to be below app bar
    ...theme.mixins.toolbar,
    justifyContent: 'flex-end',
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing(3),
    transition: theme.transitions.create('margin', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    marginLeft: -240,
  },
  contentShift: {
    transition: theme.transitions.create('margin', {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
    marginLeft: 0,
  },
});

class App extends Component {
	constructor(props) {
		super(props);
		this.state = {
			adminDia: false,
			adminMode: false,
			adminTable: 0,
			pwd: '',
			enteredPwd: '',
		};
		this.modeChange = this.modeChange.bind(this);
		this.checkPwd = this.checkPwd.bind(this);
	}


	modeChange () {
		const { adminMode } = this.state
		if (adminMode){
			this.setState({adminMode:false})
		} else {
			this.setState({adminDia:true});
		}
	}

	checkPwd = (e) => {
		e.preventDefault();
		const {pwd, enteredPwd} = this.state;
		if (pwd === enteredPwd){
			this.setState({adminMode:true})
		}

		this.setState({adminDia:false, enteredPwd:''})
	}

	searchTable = (e) =>{
		const selectedTable = e.target.textContent

		if (selectedTable === "GCN Viewer"){
			this.setState({adminTable: 0})
		} else if (selectedTable === "Event Table"){
			this.setState({adminTable: 1})
		} else if (selectedTable === "Circular Table"){
			this.setState({adminTable: 2})
		} else if (selectedTable === "Observatory Table"){
			this.setState({adminTable: 3})
		} else if (selectedTable === "Notice Table"){
			this.setState({adminTable: 4})
		}

	}
	
	render(){
		const { adminMode, adminTable } = this.state;
		const { classes } = this.props;

		return (
		<div className={classes.root}>
			<AppBar         
				position="fixed"
		        className={clsx(classes.appBar, {[classes.appBarShift]: adminMode})}
	        >
				<Toolbar className={classes.toolbar}>
				<Typography component="h1" variant="h6" color="inherit" noWrap className={classes.title}>
				<div style={{fontSize: "1.2em", fontWeight:"1000", paddingBottom:"-5pt"}}> GCN Circular Viewer </div>
				<div style={{fontSize: "0.8em", fontWeight:"100"}}> Time-domain Astronomy Coordination Hub (TACH) </div>
				</Typography>
				<IconButton
		            color="inherit"
		            aria-label="open drawer"
		            onClick={ this.modeChange }
		            edge="end"
		            className={clsx(classes.menuButton, adminMode && classes.hide)}
          		>
					<SupervisorAccountIcon />
				</IconButton>
				</Toolbar>
			</AppBar>
			<Drawer
				className={classes.drawer}
		        variant="persistent"
		        anchor="left"
		        open={adminMode}
		        classes={{
		          paper: classes.drawerPaper,
		        }}
	        >
	        <div className={classes.drawerHeader}>
				<IconButton onClick={this.modeChange}>
	            	<ExitToAppIcon />
	        	</IconButton>
	        </div>
	        <Divider />
	        <List onClick= {e => this.searchTable(e)}>{mainListItems}</List>
	        <Divider />
	        <List onClick= {e => this.searchTable(e)}>{adminListItems}</List>
	      	</Drawer>
	      	<main
        		className={clsx(classes.content, {
          		[classes.contentShift]: adminMode, })}
      		>
        	<div className={classes.drawerHeader} />
	      	
			{(adminMode) ? <ADMIN adminTable={adminTable} />:<GCN_TABLE />}

			<div>
				<Dialog
	            open={ this.state.adminDia }
	            maxWidth={"md"}
	            aria-labelledby="alert-dialog-title"
	            aria-describedby="alert-dialog-description"
	            >
	            <DialogTitle id="alert-dialog-title"><div style={{fontSize:"0.9em"}}>Admin</div></DialogTitle>
	                <DialogContent>
	                <Container component="main" maxWidth="xs">
				      <CssBaseline />
				      <div className={styles.paper}>
				        <form noValidate>
				          <TextField
				            variant="outlined"
				            margin="normal"
				            required
				            fullWidth
				            name="password"
				            label="Password"
				            type="password"
				            id="password"
				            autoComplete="current-password"
				            size="small"
				            value={this.state.enteredPwd}
				            onChange = {(evt) => this.setState({enteredPwd: evt.currentTarget.value})}
				          />
				          <Button
				            type="submit"
				            fullWidth
				            variant="contained"
				            color="primary"
				            size="small"
				            onClick = {e => this.checkPwd(e)}
				          >
				            Enter
				          </Button>
				        </form>
				      </div>
				    </Container>
				    </DialogContent>
	            </Dialog>
            </div>
            
            <Box pt={4}>
            <Typography variant="body2" color="textSecondary" align="right">
			Contact: Donggeun Tak  
			<a href="mailto:donggeun.tak@gmail.com"><MailOutlineIcon style={{color:"#454545", fontSize:"10pt"}}/></a>
	        </Typography> 
	        
          	</Box>
          	</main>
		</div>
		);
	};

}



export default withStyles(useStyles)(App);



    
